.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
    transition: 0.8s;
}

.header-bg {
    background-color: rgba(0, 0, 0, 0.85);
    transition: 0.8s;
}

.header h1 {
    font-family: 'Fasthand';
    letter-spacing: 5.5px;
    font-size: 20;
    font-weight: 500;
    /* text-decoration: none; */
}

.header h1:hover {
    color: yellow;
    transition: 1s;
}

.nav-menu {
    display: flex;
    list-style: none;
}

.nav-menu li {
    padding: 0 1rem;
}

.nav-menu li a {
    font-size: 1.2rem;
    font-weight: 200;
    /* text-decoration: none; */
}

.nav-menu li a:hover {
    color: yellow;
    font-weight: 300;
}

.hamburger {
    display: none;
}

@media screen and (max-width: 768px) {
    .nav-menu {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, alpha0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.18s;
        background-color: black;
    }

    .nav-menu.active {
        left: 0%;
    }

    .nav-menu li {
        padding: 1rem;
    }

    .nav-menu li a {
        font-size: 1.5rem;
    }

    .hamburger {
        display: initial;
    }
}

@media screen and (min-width: 0px) and (max-width: 320px) {
    
    .nav-menu li a {
        font-size: 1rem;
    }

}
